import React from 'react';
import '../../css/box-dish.css';


class BoxDish extends React.Component{
    constructor(props){
        super(props);
        this.state={
            titleDish:['Чебурашка','Крокодил Гена','Шапокляк','Крыса Лариса','Жук-пшук'],
            price:'start',
            value: '',
            status: false
        }
        // const titleDish = this.state.titleDish;
        // const value = this.state.value
        // value={this.state.value}
        // onChange={(event)=>{this.testFunc1(event); this.testFunc2()}
    }
    selectDish(){
        return (
            <form>
                <select onChange={(event)=>{this.testFunc1(event)}}>
                    <option></option>
                    <option value='Чебурашка'>{this.state.titleDish[0]}</option>
                    <option value='Крокодил Гена'>{this.state.titleDish[1]}</option>
                    <option value='Шапокляк'>{this.state.titleDish[2]}</option>
                    <option value='Крыса Лариса'>{this.state.titleDish[3]}</option>
                    <option value='Жук-пшук'>{this.state.titleDish[4]}</option>
                </select>
            </form>
        )
    }
    testFunc1(event){
            this.setState({value:event.target.value});

            switch(event.target.value){
                case this.state.titleDish[0]:
                    this.setState({price:event.target.value,status:true})
                    break
                case this.state.titleDish[1]:
                     this.setState({price:event.target.value,status:true})
                    break
                case this.state.titleDish[2]:
                     this.setState({price:event.target.value,status:true})
                    break
                case this.state.titleDish[3]:
                     this.setState({price:event.target.value,status:true})
                    break
                case this.state.titleDish[4]:
                     this.setState({price:event.target.value,status:true})
                    break
                default:
                    return this.state.price[1]
            }
    }
    testFunc2(func1,func2){
        if(this.state.status===false){
            return func1
        }else {return func2}
    }

    render(){
        console.log(
        `value: ${this.state.value}
        state-price: ${this.state.price}
        title: ${this.state.titleDish[0]}`)
        return(
            <article>
                <div style={{background:`url(${this.props.src})no-repeat center`, backgroundSize:'100% 100%'}}></div>
                <div>
                    <div className="title-box">
                        {this.testFunc2(this.selectDish(),this.state.price)}
                            <p></p>
                            <p>Короткий опис блюда</p>
                        
                    </div>
                    
                    <div className="price-box">
                        <div>
                            <p>{this.state.price}</p>
                            <p>Total coast</p>
                        </div>
                    </div>
                </div>
            </article>
        )
    }
}

export default BoxDish