import React from 'react';
import TitleH from '../header_components/title_transform';
import imgBuff from '../../img/buffet-backgr.png';
import BoxDish from '../optional-components/box-dish';
import imgDish from '../../img/test_1.jpg';
// import { ViberClient } from 'messaging-api-viber';


const titlePage = "Menu furshet";
const backImage = {
    background: `url(${imgBuff})no-repeat`,
    width:'550px',
    height: '150px',
    margin: 'auto',
    padding:'110px 0 0 0'
}
function pageUrlsLocalStor(){
    localStorage.setItem('URL_Pages', `${window.location.pathname}`)
  }
export default (props)=>{
    pageUrlsLocalStor()
    return (
        <div>
            <div style={backImage}>
                <TitleH headers={titlePage} />
            </div>
            <BoxDish 
                src={imgDish}
            />
        </div>
    )
}