import React from "react";
import {NavLink} from 'react-router-dom';
import '../../css/style-side/header-style/header.css';
import logo from '../../img/logo.png';
import {PUBLIC_URL} from '../../page-admin-securitu/urls-firebase';
import {connect} from 'react-redux';


function Header(props){
  return(
    <header className={props.nameClass}>
      <div className="conteiner">
        <div className="top-header">

        <NavLink 
          to={`${PUBLIC_URL}`}
          exact
          onClick={props.onMain}
          style={{textDecoration:'none', color:'inherit'}}
        >
          <div className="main-logo pos-rel">
            <img src={logo} width="36" height="36" alt="Our logo"/>
          </div>
        </NavLink>

          <div className="test">
            <ul className="menu">
              <li>
              <NavLink 
                  to={`${PUBLIC_URL}`}
                  exact
                  onClick={props.onMain}
                  style={{textDecoration:'none', color:'inherit'}}
                  activeClassName='name-active-h'
                  >головна</NavLink></li>
              <li><NavLink>буфет</NavLink></li>
              <li><NavLink 
                  to={`${PUBLIC_URL}furshet`}
                  onClick={props.onFurshet}
                  style={{textDecoration:'none', color:'inherit'}}
                  activeClassName='name-active-f'
                  className="link"
                  >фуршети</NavLink></li>
              <li><NavLink
                  to={`${PUBLIC_URL}administrator`}
                  onClick={props.onAdmin}
                  style={{textDecoration:'none', color:'inherit'}}
                  activeClassName='name-active-pizza'
                >піца</NavLink></li>
              <li><NavLink>контакти та замовлення</NavLink></li>
              <li className={`sliderx ${props.sliderXpos}`}></li>
            </ul>
          </div>
        </div>
      </div> 
    </header>
    )
}

function mapStateToProps(state){
  console.log(state)
  return{
    nameClass:state.headerReduser.nameClass,
    sliderXpos:state.headerReduser.sliderXpos
  }
}

function mapDispatchToProps(dispatch){
  return{
    onMain:()=>dispatch({type:'MAIN'}),
    onFurshet:()=>dispatch({type:'FURSHET'}),
    onAdmin:()=>dispatch({type:'ADMIN'}),
  }
}


export default connect(mapStateToProps,mapDispatchToProps)(Header)