import React from 'react';
import TitleH from '../components/header_components/title_transform';
import './styles-security-pages/admin-page.css';
import BoxDish from '../components/optional-components/box-dish';
// import { thisExpression } from '@babel/types';
// const { ViberClient } = require('messaging-api-viber');



function pageUrlsLocalStor(){
    localStorage.setItem('URL_Pages', `${window.location.pathname}`)
  }

function TestFunk0(props){
    console.log(props.data.boxes)
    return(
        <div className="admin-furhet-field">{props.data.boxes.map(item =>{
            return <div>{item}</div>
        })}</div>
    )
}



class Admin extends React.Component{
    constructor(props){
        super(props);
        this.state={
            boxes:[],
            returnBox:''
        }
        console.log("constructor")
    }

    creatState = (n) =>{
        // console.log(n)
        const divs = []
        for(let i=0; i<n; i++){
            divs[i] = <BoxDish/>
        }
        this.setState({
            boxes: divs
        })
    }
    testFunk = (data) =>{
        return(
            <TestFunk0 data={data}/>
        )
    }

    testFunk2=()=>{
        return(
            this.setState({
                returnBox: this.testFunk(this.state)
            }) 
        )
    }
    
    render(){
        pageUrlsLocalStor()
        console.log('render')
        return(
            <div>
                <TitleH headers={'Admin'}/>
                <div className="admin-block-1">
                    <h4>№1 Кількість блоків з блюдами</h4>
                    <input type="number" placeholder="к-ть блюд" onChange={event =>this.creatState(event.target.value)}/>
                </div>
                <hr/>
                <TestFunk0 data={this.state}/>
                <button onClick={this.testFunk2}>Post</button>
                {this.state.returnBox}
            </div>
        )
    }
}

export default Admin