const urlPages = localStorage.getItem('URL_Pages')

function findClassName(urlPages){
    if(urlPages==='/'){
        return 'homepage-header'
    }else if (urlPages==='/furshet'){
        return 'furshet-header'
    }else if(urlPages==='/administrator'){
        return 'admin-header'
    }else{return 'homepage-header'}
}

function findPosition(urlPages){
    if(urlPages==='/'){
        return 'position-h'
    }else if (urlPages==='/furshet'){
        return 'position-f'
    }else if(urlPages==='/administrator'){
        return 'position-pizza'
    }else{return 'position-h'}
}

const headerColorStatic={
    nameClass: findClassName(urlPages),
    sliderXpos: findPosition(urlPages)
}

export default function headerReduser(state = headerColorStatic, action){
    switch(action.type){
        case 'MAIN':
            return{
                nameClass: 'homepage-header',
                sliderXpos: 'position-h'
            }
        case 'FURSHET':
            return{
                nameClass: 'furshet-header',
                sliderXpos: 'position-f'
            }
        case 'ADMIN':
            return{
                nameClass: 'admin-header',
                sliderXpos: 'position-pizza'
            }
        default:
            return state
    }
}